<template>
    <div class="cinema_body" :style="mystyle">
            <ul>
              <!-- <li>
                <div>
                  <span>大地影院(澳东世纪店)</span>
                  <span class="q"><span class="price">22.9</span> 元起</span>
                </div>
                <div class="address">
                  <span>金州区大连经济技术开发区澳东世纪3层</span>
                  <span>1763.5km</span>
                </div>
                <div class="card">
                          <div>小吃</div>
                          <div>折扣卡</div>
                    </div>
              </li> -->
              <li v-for="data in datalist" :key="data.cinemaId" @click="goTicket(data)" >
                <div>
                  <span>{{data.name}}</span>
                  <span class="q"><span class="price">{{data.lowPrice/100}}</span> 元起</span>
                </div>
                <div class="address">
                  <div class="van-ellipsis">{{data.address}}</div>
                  <span>{{Math.round(data.longitude - data.latitude/2)}}</span>
                </div>
                <div class="card">
                          <div>小吃</div>
                          <div>折扣卡</div>
                    </div>
              </li>
              
            </ul>
        </div>
</template>

<script>
import BScroll from '@better-scroll/core'
export default {
    name:'Cilist',
    data() {
      return {
        datalist:[],
        mystyle:{
          height:'0px'
        }
      }
    },
    activated() {
      console.log(this.$store.state.city.cityId);
      // let cityid = JSON.stringify(this.$store.state.city.cityId)
      let cityid = this.$store.state.city.cityId
      console.log(cityid);
    this.mystyle.height = document.documentElement.clientHeight - 50 + 'px'
		this.axios({
			// url:'https://m.maizuo.com/gateway?cityId=' + cityid,
			url:`https://m.maizuo.com/gateway?cityId=${cityid}`,
			headers:{
				'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16348321693295459686744065"}',
				'X-Host': 'mall.film-ticket.cinema.list'
			}
		}).then(res=>{
			console.log(res);
			this.datalist = res.data.data.cinemas
      // console.log(this.BetterScroll(".cinema"));

      // 滚动效果
      this.$nextTick(()=>{
        new BScroll(".cinema_body",{
          scrollbar:{
            fade:true,
            interactive:false
          }
        })
      })
		})
	},
  methods: {
    goTicket(data){
      console.log(data.cinemaId);
      var cinemaId = data.cinemaId
      this.$router.push('/ticket' );
      // this.$router.push({ path: '/ticket', params: { cinemaId }});
      this.$bus.$emit('updateData',data.name)
    }
  },
  

}
</script>

<style scoped>
#content .cinema_body[data-v-39616362] {
    flex: 1;
    overflow: hidden;
}
.cinema_body {height: 600px; }
